document.addEventListener('DOMContentLoaded', function() {
  const ele = document.querySelector('.recommendation-wall');
  ele.style.cursor = 'grab';
  let pos = { top: 0, left: 0, x: 0, y: 0 };
  const mouseDownHandler = function(e) {
      ele.style.cursor = 'grabbing';
      ele.style.userSelect = 'none';

      pos = {
          left: ele.scrollLeft,
          top: ele.scrollTop,
          // Get the current mouse position
          x: e.clientX,
          y: e.clientY,
      };

      document.addEventListener('mousemove', mouseMoveHandler);
      document.addEventListener('mouseup', mouseUpHandler);
  };
  const mouseMoveHandler = function(e) {
      // How far the mouse has been moved
      const dx = e.clientX - pos.x;
      const dy = e.clientY - pos.y;

      // Scroll the element
      ele.scrollTop = pos.top - dy;
      ele.scrollLeft = pos.left - dx;
  };
  const mouseUpHandler = function() {
      ele.style.cursor = 'grab';
      ele.style.removeProperty('user-select');

      document.removeEventListener('mousemove', mouseMoveHandler);
      document.removeEventListener('mouseup', mouseUpHandler);
  };
  // Attach the handler
  ele.addEventListener('mousedown', mouseDownHandler);
});
// menu 切換
let menuOpenBtn = document.querySelector('.menuToggle');
let linkBtn = document.querySelectorAll('.topBar-menu a');
let menu = document.querySelector('.topBar-menu');
menuOpenBtn.addEventListener('click', menuToggle);

linkBtn.forEach((item) => {
  item.addEventListener('click', closeMenu);
})

function menuToggle() {
  if(menu.classList.contains('openMenu')) {
      menu.classList.remove('openMenu');
  }else {
      menu.classList.add('openMenu');
  }
}
function closeMenu() {
  menu.classList.remove('openMenu');
}



//開始寫作業
const api_path = 'jslive2022';
const https = 'https://livejs-api.hexschool.io/api/livejs/v1/customer/';

//重構：初始化部分
function init(){
  getProductList();
};
init();
//重構：組字串函式
function addProductHTML(item){
  return  `
  <li class="productCard">
    <h4 class="productType">新品</h4>
    <img src="${item.images}" alt="">
    <a href="#" class="addCardBtn" data-id="${item.id}">加入購物車</a>
    <h3>${item.title}</h3>
    <del class="originPrice">NT$${item.origin_price}</del>
    <p class="nowPrice">NT$${item.price}</p>
  </li>`;
}
function addCartListHTML(item){
  return `
  <tr>
    <td>
      <div class="cardItem-title">
          <img src="${item.product.images}" alt="">
          <p>${item.product.title}</p>
      </div>
    </td>
    <td>NT$${item.product.price}</td>
    <td>${item.quantity}</td>
    <td>NT$${item.product.price * item.quantity}</td>
    <td class="discardBtn">
      <a href="#" class="material-icons" data-id="${item.id}">
          clear
      </a>
    </td>
  </tr>`
};

let productData = [];
let cartData = [];
const productList = document.querySelector('.productWrap');
const productSelect = document.querySelector('.productSelect');
const cartList = document.querySelector('.shoppingCart-tableBody')


//產品列表：接 API
function getProductList(){
  axios.get(`${https}${api_path}/products`)
  .then(function(res){
    productData = res.data.products;
    renderProductList(productData);
  })
  .catch(function (error) {
    console.log(error);
  })
};

//產品列表：渲染
function renderProductList(productData){
  let str = '';
  productData.forEach(function(item){
    str += addProductHTML(item);
    productList.innerHTML = str;
  })
};

//下拉選單：監聽
productSelect.addEventListener('change',function(e){
  const category = e.target.value;
  //下拉選單：判斷篩選
  if (category == '全部'){
    renderProductList(productData);
    return;
  }
  let str = '';
  productData.forEach(function(item){
    //篩選出相同的category
    if(item.category == category){
      str +=addProductHTML(item);
      productList.innerHTML = str;
    }
  })
});

//加入購物車：監聽按鈕
productList.addEventListener('click',function(e){
  //取消預設
  e.preventDefault();
  const addCartClass = e.target.getAttribute('class');
  //排除按鈕以外（按鈕一顆一顆綁監聽，會降低效能 => 監聽都寫在外層）
  if (addCartClass !== 'addCardBtn'){
    return;
  };
  let productId = e.target.getAttribute('data-id');
  let numCheck = 1;
  cartData.forEach(function(item){
    //判斷點擊到的id：有的話在原本 data 的 quantity +1
    if(item.product.id === productId){
      numCheck = item.quantity += 1;
    }
  })
  //post
  axios.post(`${https}${api_path}/carts`,{
    "data": {
      "productId": productId,
      "quantity": numCheck
    }
  }).then(function(res){
    alert('加入購物車')
    //重新渲染，刷新狀態
    getCartList();
  })
});

//購物車列表：接 API
function getCartList(){
  axios.get(`${https}${api_path}/carts`)
  .then(function(res){
    document.querySelector('.totalPrice').textContent = res.data.finalTotal;
    cartData = res.data.carts;
    let str = '';
    cartData.forEach(function(item){
      str += addCartListHTML(item);
      cartList.innerHTML = str;
    })
  })
};

//刪除購物車-單筆
cartList.addEventListener('click',function(e){
  e.preventDefault();
  const cartId = e.target.getAttribute('data-id');
  if (cartId == null){
    return;
  }
  axios.delete(`${https}${api_path}/carts/${cartId}`)
  .then(function(res){
    alert('單筆刪除成功');
    getCartList();
  })
});
//刪除購物車-全部
const discardAllBtn = document.querySelector('.discardAllBtn');

discardAllBtn.addEventListener('click',function(e){
  e.preventDefault();
  axios.delete(`${https}${api_path}/carts`)
  .then(function(res){
    alert('全部刪除成功');
    getCartList();
  })
  //錯誤判斷：已清空
  .catch(function(res){
    alert('購物車已清空，請勿重複點擊')
  })
});

//產生訂單
const orderInfoBtn = document.querySelector('.orderInfo-btn');
orderInfoBtn.addEventListener('click',function(e){
  e.preventDefault();
  //確認條件
  if (cartData.length === 0){
    alert('請加入商品至購物車');
    return;
  };
  const customerName =document.querySelector('#customerName').value;
  const customerPhone =document.querySelector('#customerPhone').value;
  const customerEmail =document.querySelector('#customerEmail').value;
  const customerAddress =document.querySelector('#customerAddress').value;
  const tradeWay =document.querySelector('#tradeWay').value;
  if (customerName=='' || customerPhone=='' || customerEmail=='' || customerAddress=='' || tradeWay==''){
    alert('請輸入訂單資訊');
    return;
  };
  axios.post(`${https}${api_path}/orders`,{
    "data": {
      "user": {
        "name": customerName,
        "tel": customerPhone,
        "email": customerEmail,
        "address": customerAddress,
        "payment": tradeWay
      }
    }
  }).then(function(res){
    alert('訂單建立成功');
    //清空填寫資料
    document.querySelector('#customerName').value = '';
    document.querySelector('#customerPhone').value = '';
    document.querySelector('#customerEmail').value = '';
    document.querySelector('#customerAddress').value = '';
    document.querySelector('#tradeWay').value = 'ATM';
    getCartList();
  })
  
});